import React from "react";
import { Link } from "react-router-dom";
import GetCart from "../../get-cart";
import CartItemPopup from "../cart-item-popup";
import "./cart-list-popup.css";
const CartListPopup = ({
	productsCart: { cartItems, orderCount, orderPrice },
	showPopup,
	showPopupList,
	productAddedToCart,
	productRemovedFromCart,
	allProductsRemovedFromCart,
}) => {
	const setClassList = showPopup ? "active" : "";
	const setClassInfoCart = orderCount > 0 ? "active" : "";
	return (
		<div className={`cart_popup_list-block ${setClassList}`}>
			<div className="cart_header-popup">
				<span>You're cart</span>
				<div onClick={showPopupList} className="close-cart-popup">
					<svg
						xmlns="http://www.w3.org/2000/svg"
						width="16"
						height="16"
						fill="#fff"
						className="bi bi-x-circle"
						viewBox="0 0 16 16"
					>
						<path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
						<path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
					</svg>
				</div>
			</div>
			<div className="cart_body-popup">
				{cartItems.length === 0
					? `You're cart is empty((`
					: cartItems.map((el) => {
							return (
								<CartItemPopup
									key={el.id}
									item={el}
									productAddedToCart={productAddedToCart}
									productRemovedFromCart={productRemovedFromCart}
									allProductsRemovedFromCart={allProductsRemovedFromCart}
								/>
							);
					  })}
			</div>
			<div className={`cart_footer-popup ${setClassInfoCart}`}>
				<div className="info-cart">
					<span>Count cart:{orderCount}</span>
					<span>Total price:{orderPrice}</span>
				</div>
				<Link to="/cart">Go to cart</Link>
			</div>
		</div>
	);
};

export default GetCart(CartListPopup);
